$font_size_normal: "1rem";
$border_control: 1px solid rgba(234, 234, 234, 1);
button {
    text-transform: none !important;
}

@mixin buttonTitleBefore($title, $translateX) {
    content: $title;
    position: absolute;
    bottom: -36px;
    z-index: 7;
    padding: 3px 6px;
    left: 50%;
    border-radius: 5px;
    background: #343a40;
    color: #fff;
    font-size: $font_size_normal;
    font-family: sans-serif;
    white-space: nowrap;
    -webkit-transform: translateX($translateX);
    -moz-transform: translateX($translateX);
    -ms-transform: translateX($translateX);
    -o-transform: translateX($translateX);
    transform: translateX($translateX);
}

@mixin buttonTitleAfter($translateX) {
    position: absolute;
    bottom: -10px;
    left: 50%;
    z-index: 7;
    color: #fff;
    border: 8px solid transparent;
    border-bottom: 8px solid #000;
    -webkit-transform: translateX($translateX);
    -moz-transform: translateX($translateX);
    -ms-transform: translateX($translateX);
    -o-transform: translateX($translateX);
    transform: translateX($translateX);
}

[button-title]:hover::before {
    @include buttonTitleBefore(attr(button-title), -50%);
}

[button-title]:hover::after {
    @include buttonTitleAfter(-50%);
}

[button-title-right]:hover::before {
    @include buttonTitleBefore(attr(button-title-right), -70%);
}

[button-title-right]:hover::after {
    @include buttonTitleAfter(-70%);
}

[button-title] {
    position: relative;
}

