    gap: 8px;
    border: $border_control;
    padding: 0.5rem;
    border-radius: 4px;
    width: auto !important;
    display: flex;
    align-items: center;
    .tag-item {
        max-width: 180px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
    .ellipsis {
